import React, { Component } from 'react';
import { connect } from 'react-redux';

class Contact extends Component {
    render() {
        return(
            <section className="contact" id="contact">
                <h1 className="section-heading">Contact</h1>
                <div className="container">
                    <div className="row">
                        <div className="col s12 m12 contact-items">
                            { this.props.contacts.map( item => {
                                return (
                                    <div className="card" key={ item.id }>
                                        <div className="card-action">
                                            <span className="card-title"><i className="material-icons margin-right">{item.con_icon}</i> { item.con_text }</span>
                                            <a href={ item.con_url }>{ item.con_account }</a>
                                        </div>
                                    </div>
                                )
                                
                            } )
                            }
                        </div>
                    </div>
                </div>
            </section>
        )
    }
}

const mapStateToProps = ( state ) => {
    return {
        contacts: state.contact.contactsInState
    }
}

export default connect(mapStateToProps)(Contact);